.scroll-prompt-left {
	position: relative;
	z-index: 1;
	/* bottom: -80px; */
	left: 35%;
	margin-top: -45px;
	margin-left: -100px;
	margin-bottom: -100px;
	width: 150px;
	height: 150px;
}

.scroll-prompt-arrow-container-left {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	animation-name: bounce_left;
	animation-duration: 1.5s;
	animation-iteration-count: infinite;
}

.scroll-prompt-arrow-left {
	animation-name: opacity;
	animation-duration: 1.5s;
	animation-iteration-count: infinite;
}

.scroll-prompt-arrow-left:last-child {
	animation-direction: reverse;
	margin-top: -8px;
	margin-left: -15px;
}

.scroll-prompt-arrow-left > div {
	width: 25px;
	height: 25px;
	border-right: 8px solid #bebebe;
	border-bottom: 8px solid #bebebe;
	transform: rotate(80deg) translateX(1px);
}

@keyframes opacity {
	0% {
		opacity: 0;
	}

	10% {
		opacity: 0.1;
	}

	20% {
		opacity: 0.2;
	}

	30% {
		opacity: 0.3;
	}

	40% {
		opacity: 0.4;
	}

	50% {
		opacity: 0.5;
	}

	60% {
		opacity: 0.6;
	}

	70% {
		opacity: 0.7;
	}

	80% {
		opacity: 0.8;
	}

	90% {
		opacity: 0.9;
	}

	100% {
		opacity: 1;
	}
}

@keyframes bounce_left {
	100% {
		transform: translateY(0) translateX(0);
	}

	90% {
		transform: translateY(-3px) translateX(3px);
	}

	80% {
		transform: translateY(-6px) translateX(6px);
	}

	70% {
		transform: translateY(-9px) translateX(9px);
	}

	60% {
		transform: translateY(-12px) translateX(12px);
	}

	50% {
		transform: translateY(-15px) translateX(15px);
	}

	40% {
		transform: translateY(-18px) translateX(18px);
	}

	30% {
		transform: translateY(-21px) translateX(21px);
	}

	20% {
		transform: translateY(-24px) translateX(24px);
	}

	10% {
		transform: translateY(-27px) translateX(27px);
	}

	0% {
		transform: translateY(-30px) translateX(30px);
	}
}
